___CSS_LOADER_EXPORT___.push([module.id, `*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header{
  text-align: center;
  color: #555555;
}

body {
    display: flex;
    flex-direction: column;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-size: 22px;
    font-weight: bold;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

main{
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 10px;
  gap: 1em;
  background-color: rgb(255, 255, 255);
}

input{
  padding: 10px 5px;
}

button{
  padding: 10px 5px;
  border: none;
}

select{
  padding: 10px 5px;
}

.res{
  font-size: 18px;
  margin-bottom: 15px;
}

ul{
  text-align: left;
  margin-left: 15px;
}

.formulario{
  display: flex;
  flex-direction: column;
  padding: 10px;
  gap: 1em;
}

.error-text{
  font-size: 0.8em;
  color: red;
}

.mes{
  display: block;
}

input:invalid {
  animation: justshake 0.3s forwards;
  color: red;
}

@keyframes justshake {
  25% {
    transform: translateX(5px);
  }

  50% {
    transform: translateX(-5px);
  }

  75% {
    transform: translateX(5px);
  }

  100% {
    transform: translateX-(5px);
  }
}

@media only screen and (min-width: 600px) {
  button:hover{
    background-color: #BDBEC7;
  }

  main{
    display: flex;
    flex-direction: column;
    width: 650px;
    margin: auto;
    padding: 10px;
    gap: 1em;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  }
}

@media only screen and (max-width: 600px){
  button:active{
    background-color: #BDBEC7;
  }

  main{
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 90%;
    gap: 1em;
  }

  body{
    font-size: 16px;
    font-weight: none;
  }
}
